<template>
<div class="c-app flex-row align-items-center" style="background:#d0dcec !important">
    <CContainer>
        <CRow class="justify-content-center">
            <CCol md="8">
                <CCardGroup>
                    <CCard class="p-4" color="primary" text-color="white">
                        <CCardBody>
                            <CForm>
                                <h3 class="font-weight-bold">Surat Elektronik</h3>
                                <p class="text-white">DPMPTSP Provinsi Kepulauan Riau</p>
                                <CInput :invalid-feedback="validationMsg($v.username)" placeholder="email" autocomplete="off" v-model.trim="$v.username.$model" :isValid="$v.username.$dirty ? !$v.username.$error : null">
                                    <template #prepend-content>
                                        <CIcon name="cil-user" /></template>
                                </CInput>
                                <CInput :invalid-feedback="validationMsg($v.password)" placeholder="Password" type="password" autocomplete="curent-password" v-model.trim="$v.password.$model" :isValid="$v.password.$dirty ? !$v.username.$error : null">
                                    <template #prepend-content>
                                        <CIcon name="cil-lock-locked" /></template>
                                </CInput>
                                <CRow>
                                    <CCol col="6" class="text-left">
                                        <CButton color="secondary" @click="toLogin()" block class="px-4">Login</CButton>
                                    </CCol>
                                    <CCol col="6" class="text-right">
                                        <CButton color="link" class="px-0">Forgot password?</CButton>
                                        <CButton color="link" class="d-lg-none">Register now!</CButton>
                                    </CCol>
                                    <CCol col="12" class="d-lg-none">
                                        <CAlert :color="message.color" closeButton v-if="message.show">
                                            <CSpinner size="sm" class="mr-3" />{{message.text}}
                                        </CAlert>
                                    </CCol>
                                </CRow>
                            </CForm>
                        </CCardBody>
                    </CCard>
                    <CCard class="text-center py-4 d-md-down-none" body-wrapper>
                        <CCardBody>
                            <img src="@/assets/images/login.png" class="img-fluid" />
                            <CAlert :color="message.color" closeButton v-if="message.show">
                                <CSpinner size="sm" class="mr-3" v-if="message.spin" />{{message.text}}
                            </CAlert>
                        </CCardBody>
                    </CCard>
                </CCardGroup>
            </CCol>
        </CRow>
    </CContainer>
</div>
</template>

<script>
import identitas from '@/containers/_identitas'
import {
    required,
    email,
    minLength
} from 'vuelidate/lib/validators'
import {
    validationMessage
} from 'vuelidate-messages'
const formMessages = {
    required: () => 'Field Tidak Boleh Kosong',
    txtMinLen: ({
        $params
    }) => `harus minimal ${$params.txtMinLen.min} karakter.`,
    email: () => `Format Email Tidak Valid`,
}
const axios = require('axios');
export default {
    name: 'Login',
    identitas,
    data() {
        return {
            username: null,
            password: null,
            message: {
                color: 'warning',
                text: null,
                show: false,
                spin: false
            }
        }
    },
    validations: {
        username: {
            required,
            txtMinLen: minLength(2),
            email
        },
        password: {
            required,
            txtMinLen: minLength(4),
        }
    },
    methods: {
        toLogin() {
            if (!this.$v.$invalid) {

                this.message.text = 'Mencari Account ...'
                this.message.color = 'info'
                this.message.show = true
                this.message.spin = true

                axios.post(this.$options.identitas[0].url + '/login', {
                        email: this.username,
                        password: this.password
                    })
                    .then((e) => {
                        e.data.code === "200" ? (this.$cookies.set('Jwt', e.data.user), this.$router.push({
                            name: e.data.redirect
                        }), this.message.text = 'account ditemukan', this.message.color = 'success', this.message.spin = false) : (this.message.text = e.data.text, this.message.color = 'danger', this.message.spin = false)

                    })
                    .catch(function (error) {
                            this.message.text = 'Terjadi Kesalahan pada jaringan'
                            this.message.color = 'danger'
                            this.message.show = true
                            this.message.spin = false
                    })
            }
        },
        validationMsg: validationMessage(formMessages)
    }
}
</script>

<style>
.invalid-feedback {
    color: white !important;
    font-weight: bold;
}
</style>
